
// import IMask from 'imask';
import Inputmask from "inputmask";


export default function form() {

  let inputs = document.querySelectorAll('input[type="text"]');
  inputs.forEach(input => {
    let placeholder = '';
    input.addEventListener('focus', function(e) {
      placeholder = e.currentTarget.placeholder;
      e.currentTarget.placeholder = "";
    })
    input.addEventListener('blur', function(e) {
      e.currentTarget.placeholder = placeholder;
    })
  })

  const forms = document.querySelectorAll('.js-form');

  const message = {
    loading: 'Загрузка данных',
    success: 'Спасибо, ваша заявка принята! Наш менеджер свяжется с вами в ближайшее время',
    failure: "Что-то пошло не так, обратитесь к администратору сайта",
  }


  if (forms.length > 0) {

    forms.forEach(form => {
      bindHandlerInputName(form);
      addMaskPhone(form);
      bindHandlerInputEmail(form);
      bindPostData(form);
    });


    function bindHandlerInputName(form) {

      const nameInput = form.querySelector('.js-name-input');
      const errorHtml = nameInput.nextElementSibling;
      const formBlock = nameInput.closest('.form__block');

      nameInput.addEventListener('input', function(e) {
        if (nameInput.value == '' || /\d/.test(nameInput.value)) {
          errorHtml.classList.add('active');
          formBlock.classList.add('error')
        } else {
          errorHtml.classList.remove('active');
          formBlock.classList.remove('error')
        }
      })

    }

    function addMaskPhone(form) {
       const phoneInput = form.querySelector('.js-phone-input');

       var im = new Inputmask("9 (999) – 999 – 99 – 99", {
            definitions: {
              '9': {
                validator: function(chrs, buffer, pos, strict, opts) {
                  if (pos === 0) {
                    return /[78]/.test(chrs);
                  }
                  if (pos === 3) {
                    return /[9]/.test(chrs);
                  }
                  return /[0-9]/.test(chrs);
                }
              }
            }
          });


          im.mask(phoneInput);


       const errorHtml = phoneInput.nextElementSibling;
       const formBlock = phoneInput.closest('.form__block');

      // проверка
      phoneInput.addEventListener('input', function(e) {

      var isCompleted = phoneInput.inputmask.isComplete();

         if (isCompleted) {
          console.log('Input mask is completed.');
          errorHtml.classList.remove('active');
          formBlock.classList.remove('error');
          return true;
          //получить
        } else {
          console.log('Input mask is not completed.');
          errorHtml.classList.add('active');
          formBlock.classList.add('error')
          return false;
        }
      })




    }

    function bindHandlerInputEmail(form) {

       const emailInput = form.querySelector('.js-email-input');
       const errorHtml = emailInput.nextElementSibling;
       const formBlock = emailInput.closest('.form__block');

      emailInput.addEventListener('input', function(e) {

        if (!(isValidEmail(emailInput.value))) {
          errorHtml.classList.add('active');
          formBlock.classList.add('error');
        } else {
          errorHtml.classList.remove('active');
          formBlock.classList.remove('error');
        }

      })

    }

    function isValidEmail(email) {
      var emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(email);
    }

    function bindPostData(form) {


      form.addEventListener('submit', function(e) {

        e.preventDefault();


        const checkName = checkValidateName(form);
        const checkPhone = checkValidatePhone(form);
        const checkEmail = checkValidateEmail(form);

        if (checkName && checkPhone && checkEmail) {

          const btn = form.querySelector('button.btn');

          const statusMessage = document.createElement('div');
          statusMessage.classList.add('form__message');
          statusMessage.innerText = message.loading;

          form.insertAdjacentElement("afterend", statusMessage );
          btn.disabled = true;

          const formData = new FormData(form);

          postData("vendor/mail.php", formData)
          .then((data) => {
            console.log(data)
            statusMessage.innerText = message.success;
          })
          .catch((err) => {
            console.log(err)
            statusMessage.innerText = message.failure;
          })
          .finally(() => {
            btn.disabled = false;
            form.reset();
            setTimeout(() => {
              statusMessage.remove()
            }, 4000);
          })

        }

      })
    }

    const postData = async (url, data) => {
      const res = await fetch(url, {
        method: "POST",
        body: data,
      });

      return await res.json();
    };


    function checkValidateName(form) {

      const nameInput = form.querySelector('.js-name-input');
      const errorHtml = nameInput.nextElementSibling;
      const formBlock = nameInput.closest('.form__block');

      if (nameInput.value == '' || /\d/.test(nameInput.value)) {
        errorHtml.classList.add('active');
        formBlock.classList.add('error')
        return false;
      } else {
        errorHtml.classList.remove('active');
        formBlock.classList.remove('error')
        return true;
      }
    }

    function checkValidatePhone(form) {
       const phoneInput = form.querySelector('.js-phone-input');
       const errorHtml = phoneInput.nextElementSibling;
       const formBlock = phoneInput.closest('.form__block');

      // проверка
      var isCompleted = phoneInput.inputmask.isComplete();

      if (isCompleted) {
        console.log('Input mask is completed.');
        errorHtml.classList.remove('active');
        formBlock.classList.remove('error');
        return true;
        //получить
      } else {
        console.log('Input mask is not completed.');
        errorHtml.classList.add('active');
        formBlock.classList.add('error')
        return false;
      }


    }


    function checkValidateEmail(form) {
       const emailInput = form.querySelector('.js-email-input');
       const errorHtml = emailInput.nextElementSibling;
       const formBlock = emailInput.closest('.form__block');

      if (!(isValidEmail(emailInput.value))) {
          errorHtml.classList.add('active');
          formBlock.classList.add('error');
          return false;
        } else {
          errorHtml.classList.remove('active');
          formBlock.classList.remove('error');
          return true;
        }
    }


  }






}
